import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { Paslon, paslon } from './paslon';
import { PdsApiService } from '../pds-api.service';

@Component({
  selector: 'app-vote',
  templateUrl: './vote.component.html',
  styleUrls: ['./vote.component.sass']
})
export class VoteComponent implements OnInit {
  state = 0;
  _paslon: Paslon;

  showPopup = false;

  constructor(private pdsApi: PdsApiService) { }

  ngOnInit(): void {
    this._paslon = paslon[0];
  }

  changeState = (s: number) => {
    this.state = s;
    if (s != 0) this._paslon = paslon[s - 1];
  }

  vote = () => {
    this.showPopup = true;
  }

  confirmVote = () => {
    this.pdsApi.vote(this.state);
    this.showPopup = false
  }

  cancelVote = () => {
    this.showPopup = false;
  }
}
