import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

import { environment } from 'src/environments/environment';
import { PopUp } from 'src/app/interfaces';
import { Router } from '@angular/router';
import { Subject } from 'rxjs';

interface UserData {
  uid: string;
  name: string;
  sid: string;
}

@Injectable({
  providedIn: 'root'
})
export class PdsApiService {
  userData: UserData;
  name: Subject<string> = new Subject<string>();
  loading: Subject<boolean> = new Subject<boolean>();
  popup: Subject<PopUp> = new Subject<PopUp>();

  constructor(private http: HttpClient, private route: Router) { }

  login(username: string, password: string){
    this.loading.next(true);
    this.http.post<UserData>(
      this.api('login'),
      {username, password}
    ).subscribe({
      next: data => {
        this.userData = data;
        this.name.next(data.name);
        this.route.navigateByUrl('/vote');
        console.log(this.userData);
        this.loading.next(false);
        this.popup.next({
          title: 'Selamat Datang',
          content: 'Perhatikan tata cara memilih. Pilihanmu menentukan masa depan STETSA',
        });
      },
      error: err => {
        console.error(err);
        this.loading.next(false);
        this.popup.next({
          title: 'Terjadi Kesalahan',
          content: 'Username atau Password salah',
        });
      }
    });
  }

  vote(val: number){
    this.loading.next(true);
    this.http.post(
      this.api('vote'),
      {
        uid: this.userData.uid,
        sid: this.userData.sid,
        value: val
      }
    ).subscribe({
      next: data => {
        console.log(data);
        this.route.navigateByUrl('/ty');
        this.loading.next(false);
      },
      error: err => {
        console.error(err);
        this.loading.next(false);
        this.popup.next({
          title: 'Terjadi Kesalahan',
          content: 'Terjadi kesalahan, silahkan coba lagi. Jika masalah tetap berlanjut silahkan hubungi panitia',
        });
      }
    });
  }

  logout(){
    this.loading.next(true);
    this.http.post(
      this.api('logout'),
      {
        uid: this.userData.uid,
        sid: this.userData.sid
      }
    ).subscribe({
      next: data => {
        console.log(data);
        this.userData = {
          uid: '',
          sid: '',
          name: '',
        };
        this.name.next('');
        this.route.navigateByUrl('/');
        this.loading.next(false);
      },
      error: err => {
        console.error(err);
        this.loading.next(false);
        this.popup.next({
          title: 'Terjadi Kesalahan',
          content: 'Terjadi kesalahan saat logout. Silahkan coba kembali',
        });
      }
    });
  }

  api(arg: string){
    return environment.apiurl + arg;
  }
}
