import { Component, OnDestroy } from '@angular/core';
import { PdsApiService } from './pds-api.service';
import { Subscription } from 'rxjs';
import { PopUp } from 'src/app/interfaces';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.sass']
})
export class AppComponent implements OnDestroy {
  name = '';
  isLoading = false;
  showPopup = false;
  popupData: PopUp = {
    title : '',
    content: '',
  };

  nameSubs: Subscription;
  loadingSubs: Subscription;
  popupSubs: Subscription;

  constructor(private pdsApi: PdsApiService) {
    this.nameSubs = pdsApi.name.subscribe(val => this.name = val);
    this.loadingSubs = pdsApi.loading.subscribe(val => this.isLoading = val);
    this.popupSubs = pdsApi.popup.subscribe(val => this.ShowPopup(val));
  }

  ngOnDestroy(): void{
    this.nameSubs.unsubscribe();
    this.loadingSubs.unsubscribe();
  }

  ShowPopup(content: PopUp){
    this.showPopup = true;
    this.popupData = content;
  }
  HidePopup(){
    this.showPopup = false;
  }
}